import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import AuthCard from '../components/AuthCard';
import TextInput from '../components/TextInput';

const Login = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const { login } = useAuth();
  const nav = useNavigate();

  const submit = async (e: React.FormEvent) => {
    e.preventDefault();
    const userToLogin = username.trim() || 'guest';
    await login(userToLogin);
    nav('/calc');
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 p-6">
      <AuthCard title={<h2 className="text-2xl font-semibold"></h2>}>
        <form onSubmit={submit}>
          <TextInput
            label=""
            value={username}
            onChange={setUsername}
            placeholder="you@example.com"
          />
          <TextInput
            label=""
            type="password"
            value={password}
            onChange={setPassword}
            placeholder=""
          />

          <button
            type="submit"
            className="w-full bg-amber-500 text-white py-3 rounded-md text-lg font-semibold mt-2 shadow-md hover:brightness-95"
          >
            
          </button>

          <div className="mt-3 text-sm text-center">
            <a href="/join" className="text-amber-500 hover:underline">
               ()
            </a>
          </div>
        </form>
      </AuthCard>
    </div>
  );
};

export default Login;
